import React, { useState, useEffect, useRef, useCallback } from 'react';
import penCursorSvg from '../assets/PenCursor.svg';
import './CustomCursor.css';

const CustomCursor = () => {
  const cursorRef = useRef(null);
  const positionRef = useRef({ x: 0, y: 0 });
  const [clicked, setClicked] = useState(false);
  const [visible, setVisible] = useState(false);
  const [isHovering, setIsHovering] = useState(false);
  const requestRef = useRef();
  const previousTimeRef = useRef();
  
  // Use a debounced hover check to reduce performance impact
  const debouncedHoverCheck = useRef(null);
  
  // Smooth cursor movement with requestAnimationFrame
  const animateCursor = useCallback((time) => {
    if (previousTimeRef.current !== undefined) {
      // Only update the DOM when visible
      if (visible && cursorRef.current) {
        cursorRef.current.style.transform = `translate3d(${positionRef.current.x}px, ${positionRef.current.y}px, 0)`;
      }
    }
    previousTimeRef.current = time;
    requestRef.current = requestAnimationFrame(animateCursor);
  }, [visible]);
  
  // Optimized hover check
  const checkHovering = useCallback((x, y) => {
    // Clear any pending hover checks
    if (debouncedHoverCheck.current) {
      clearTimeout(debouncedHoverCheck.current);
    }
    
    // Debounce hover checks to reduce performance impact
    debouncedHoverCheck.current = setTimeout(() => {
      const element = document.elementFromPoint(x, y);
      if (!element) {
        setIsHovering(false);
        return;
      }
      
      const clickableElements = ['BUTTON', 'A', 'INPUT', 'TEXTAREA', 'SELECT'];
      const isHoveringElement = 
        clickableElements.includes(element.tagName) || 
        element.closest('button') || 
        element.closest('a') ||
        element.getAttribute('role') === 'button';
      
      setIsHovering(isHoveringElement);
    }, 50); // 50ms debounce
  }, []);

  useEffect(() => {
    // Hide the default cursor
    document.body.style.cursor = 'none';
    
    // Start the animation loop
    requestRef.current = requestAnimationFrame(animateCursor);
    
    const updatePosition = (e) => {
      // Get the actual mouse position
      const x = e.clientX;
      const y = e.clientY;
      
      // Update the position reference with an offset for the pen tip
      positionRef.current = {
        x: x - 15, // Offset to position the pen tip at the cursor
        y: y - 75  // Offset to position the pen tip at the cursor
      };
      
      setVisible(true);
      checkHovering(x, y);
    };

    const handleMouseDown = () => setClicked(true);
    const handleMouseUp = () => setClicked(false);
    const handleMouseLeave = () => setVisible(false);
    const handleMouseEnter = () => setVisible(true);

    // Use passive event listeners for better performance
    document.addEventListener('mousemove', updatePosition, { passive: true });
    document.addEventListener('mousedown', handleMouseDown, { passive: true });
    document.addEventListener('mouseup', handleMouseUp, { passive: true });
    document.addEventListener('mouseleave', handleMouseLeave, { passive: true });
    document.addEventListener('mouseenter', handleMouseEnter, { passive: true });

    return () => {
      // Clean up animation frame
      cancelAnimationFrame(requestRef.current);
      
      // Clear any pending hover checks
      if (debouncedHoverCheck.current) {
        clearTimeout(debouncedHoverCheck.current);
      }
      
      // Restore the default cursor
      document.body.style.cursor = 'auto';
      
      // Remove event listeners
      document.removeEventListener('mousemove', updatePosition);
      document.removeEventListener('mousedown', handleMouseDown);
      document.removeEventListener('mouseup', handleMouseUp);
      document.removeEventListener('mouseleave', handleMouseLeave);
      document.removeEventListener('mouseenter', handleMouseEnter);
    };
  }, [animateCursor, checkHovering]);

  // Only render the custom cursor if it's visible
  if (!visible) return null;

  return (
    <div
      ref={cursorRef}
      className={`custom-cursor ${clicked ? 'clicked' : ''} ${isHovering ? 'hovering' : ''}`}
      style={{
        transform: `translate3d(${positionRef.current.x}px, ${positionRef.current.y}px, 0)`,
      }}
    >
      <div className="cursor-outline">
        <img src={penCursorSvg} alt="" />
      </div>
    </div>
  );
};

export default CustomCursor;
