import React, { useState, useEffect, useRef } from "react";
import { useRive } from "rive-react";
import "./ExcavationReveal.css";
import "./fonts.css";

const ExcavationReveal = ({ title, subtitle }) => {
  const [revealed, setRevealed] = useState(false);
  const [cracks, setCracks] = useState([]);
  const [clickCount, setClickCount] = useState(0);
  const [mistyBlobs, setMistyBlobs] = useState([]);
  const [navbarMistBlobs, setNavbarMistBlobs] = useState([]);
  const containerRef = useRef(null);
  const clangSoundRef = useRef(null);
  const shineSoundRef = useRef(null);

  // Use Rive with state machine
  const { RiveComponent, rive } = useRive({
    src: `${process.env.PUBLIC_URL}/hero-page/catsmith.riv`,
    autoplay: true,
    stateMachines: "State Machine 1",
    onStateChange: (event) => {
      // Check for animation state changes
      if (event.data && event.data.includes('hammer_start')) {
        // setIsAnimating(true);
      }
      if (event.data && event.data.includes('hammer_end')) {
        // setIsAnimating(false);
      }
    },
    onError: (err) => {
      console.error("Rive animation error:", err);
      // Continue without animation
    }
  });

  useEffect(() => {
    // Generate random elements when component loads
    // Generate orbs instead of cracks
    const newOrbs = [];
    // Create a larger pool of orbs that will be revealed progressively with each click
    for (let i = 0; i < 45; i++) { 
      // Create more varied sizes - some big, some medium, some small
      let sizeCategory;
      const randomValue = Math.random();
      if (randomValue < 0.15) {
        // 15% chance for extra large orbs
        sizeCategory = 2.0 + Math.random() * 1.5; // 2.0-3.5
      } else if (randomValue < 0.35) {
        // 20% chance for large orbs
        sizeCategory = 1.3 + Math.random() * 0.7; // 1.3-2.0
      } else if (randomValue < 0.65) {
        // 30% chance for medium orbs
        sizeCategory = 0.7 + Math.random() * 0.6; // 0.7-1.3
      } else if (randomValue < 0.85) {
        // 20% chance for small orbs
        sizeCategory = 0.3 + Math.random() * 0.4; // 0.3-0.7
      } else {
        // 15% chance for tiny orbs
        sizeCategory = 0.1 + Math.random() * 0.2; // 0.1-0.3
      }
      
      // Assign one of the four colors
      const colorTypes = ['gold', 'cream', 'peach', 'rose'];
      const colorType = colorTypes[Math.floor(Math.random() * colorTypes.length)];
      
      newOrbs.push({
        top: Math.random() * 100,
        left: Math.random() * 100,
        size: sizeCategory,
        delay: Math.random() * 1.5, // Reduced delay for quicker start
        duration: 5 + Math.random() * 8, // Significantly faster animation (was 15-35s, now 5-13s)
        colorType: colorType // Add color type property
      });
    }
    setCracks(newOrbs); // Still using the cracks state but for orbs
    
    // Generate fireflies with different colors
    const newMistyBlobs = [];
    const colors = ['pink', 'salmon', 'paleyellow', 'lightpurpleblue', 'orangered'];
    
    for (let i = 0; i < 30; i++) { 
      const randomColor = colors[Math.floor(Math.random() * colors.length)];
      newMistyBlobs.push({
        top: Math.random() * 100,
        left: Math.random() * 100,
        size: 4 + Math.random() * 10, 
        opacity: 0.2 + Math.random() * 0.5,
        animationDuration: 10 + Math.random() * 20, 
        glowDuration: 2 + Math.random() * 4,
        color: randomColor
      });
    }
    setMistyBlobs(newMistyBlobs);
    
    // Generate navbar mist blobs
    const newNavbarMistBlobs = [];
    for (let i = 0; i < 12; i++) {
      newNavbarMistBlobs.push({
        top: Math.random() * 100,
        left: `${Math.random() * 100}%`,
        size: 30 + Math.random() * 100,
        opacity: 0.1 + Math.random() * 0.2,
        animationDuration: 15 + Math.random() * 30
      });
    }
    setNavbarMistBlobs(newNavbarMistBlobs);
    
    // Initialize sound effects
    const clangSound = new Audio('/sounds/metal_clang.mp3');
    clangSound.volume = 0.5;
    clangSoundRef.current = clangSound;
    
    const shineSound = new Audio('/sounds/shine1.mp3');
    shineSound.volume = 0.6;
    shineSoundRef.current = shineSound;
    
    // Cleanup function
    return () => {
      // Clean up sound references
      clangSoundRef.current = null;
      shineSoundRef.current = null;
    };
  }, []);

  // Handle wall clicks (separate from Rive clicks)
  const handleWallClick = () => {
    // Play clang sound immediately on click
    if (clangSoundRef.current) {
      setTimeout(() => {
        clangSoundRef.current.currentTime = 0;
        clangSoundRef.current.play().catch(err => console.error("Error playing clang sound:", err));
      }, 500);
    }
    
    // Trigger animation via state machine
    if (rive) {
      try {
        // Different ways to access Rive inputs based on the API version
        if (rive.stateMachines && rive.stateMachines.length > 0) {
          const stateMachine = rive.stateMachines[0];
          if (stateMachine && typeof stateMachine.fire === 'function') {
            stateMachine.fire('Trigger');
          }
        } else if (rive.contents && rive.contents.stateMachines) {
          // Try to find the trigger input
          const stateMachine = rive.contents.stateMachines[0];
          if (stateMachine && stateMachine.inputs) {
            const triggerInput = stateMachine.inputs.find(input => input.name === 'Trigger');
            if (triggerInput) {
              triggerInput.fire();
            }
          }
        } else {
          console.log("Unable to find Rive state machine trigger");
        }
      } catch (err) {
        console.error("Error triggering Rive animation:", err);
      }
    }
    
    if (clickCount < 3) {
      const newCount = clickCount + 1;
      setClickCount(newCount);
      
      // On the 3rd click, reveal the content and play shine sound
      if (newCount === 3) {
        setTimeout(() => {
          // Play shine sound when wall disappears
          if (shineSoundRef.current) {
            shineSoundRef.current.currentTime = 0;
            shineSoundRef.current.play().catch(err => console.error("Error playing shine sound:", err));
          }
          setRevealed(true);
        },1500);
      }
    } else if (!revealed) {
      // Play shine sound when wall disappears
      if (shineSoundRef.current) {
        shineSoundRef.current.currentTime = 0;
        shineSoundRef.current.play().catch(err => console.error("Error playing shine sound:", err));
      }
      setRevealed(true);
    }
  };

  // Handle hover effect for misty blobs
  const handleMouseMove = (e) => {
    if (!containerRef.current) return;
    
    const rect = containerRef.current.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;
    
    // Add a class to misty blobs near the cursor
    const blobElements = document.querySelectorAll('.misty-blob');
    blobElements.forEach(blob => {
      const blobRect = blob.getBoundingClientRect();
      const blobCenterX = blobRect.left + blobRect.width / 2 - rect.left;
      const blobCenterY = blobRect.top + blobRect.height / 2 - rect.top;
      
      const distance = Math.sqrt(
        Math.pow(x - blobCenterX, 2) + 
        Math.pow(y - blobCenterY, 2)
      );
      
      if (distance < 150) {
        blob.classList.add('dispelling');
      } else {
        blob.classList.remove('dispelling');
      }
    });
  };

  return (
    <div 
      className="excavation-container" 
      ref={containerRef}
      onMouseMove={handleMouseMove}
      onClick={handleWallClick}
    >
      {/* Rive animation layer - now on top with its own click handling */}
      <div className="rive-container">
        <RiveComponent />
      </div>
      
      {/* Wall layer */}
      <div 
        className={`wall ${clickCount > 0 ? 'cracking' : ''} ${revealed ? 'revealed' : ''}`}
        style={{ backgroundImage: `url(${process.env.PUBLIC_URL}/hero-page/ImprovedWallShort1.png)` }}
      >
        {/* Navbar mist effect */}
        <div className="navbar-mist">
          {navbarMistBlobs.map((blob, index) => (
            <div 
              key={`nav-blob-${index}`}
              className="navbar-mist-blob"
              style={{
                top: `${blob.top}%`,
                left: blob.left,
                width: `${blob.size}px`,
                height: `${blob.size}px`,
                opacity: blob.opacity,
                animationDuration: `${blob.animationDuration}s`
              }}
            />
          ))}
        </div>
        
        {!revealed && (
          <div className="click-instruction">
            {clickCount === 0 ? "Click to Forge" : `${3 - clickCount} more to Reveal`}
          </div>
        )}
        
        {/* Orbs that appear on clicks */}
        {clickCount > 0 && cracks.slice(0, clickCount * 15).map((orb, index) => (
          <div 
            key={`orb-${index}`}
            className={`orb orb-${orb.colorType}`}
            style={{
              top: `${orb.top}%`,
              left: `${orb.left}%`,
              width: `${orb.size * 30}px`, /* Base size multiplied by scale factor */
              height: `${orb.size * 30}px`, /* Base size multiplied by scale factor */
              animationDelay: `${orb.delay}s`,
              animationDuration: `${orb.duration}s`
            }}
          />
        ))}
      </div>
      
      {/* Hidden content underneath */}
      <div className="hidden-content">
        <div 
          className="magical-background" 
          style={{ backgroundImage: `url(${process.env.PUBLIC_URL}/hero-page/heroBG.png)` }}
        />
        
        {/* Fireflies that can be dispelled */}
        {mistyBlobs.map((blob, index) => (
          <div 
            key={`blob-${index}`}
            className={`misty-blob firefly-${blob.color}`}
            style={{
              top: `${blob.top}%`,
              left: `${blob.left}%`,
              width: `${blob.size}px`,
              height: `${blob.size}px`,
              opacity: blob.opacity,
              animationDuration: `${blob.animationDuration}s, ${blob.glowDuration}s`
            }}
          />
        ))}
        
        <div className={`text-reveal ${revealed ? 'visible' : ''}`}>
          <h1 className="reveal-title">{title}</h1>
          <h2 className="reveal-subtitle">{subtitle}</h2>
        </div>
      </div>
    </div>
  );
};

export default ExcavationReveal;
