import React, { useState, useEffect, useRef } from 'react';
import './ImageSlideshow.css';

const ImageSlideshow = ({ images, alt, autoplaySpeed = 5000 }) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isPaused, setIsPaused] = useState(false);
  const [isZoomed, setIsZoomed] = useState(false);
  const timerRef = useRef(null);

  // Function to move to the next slide
  const nextSlide = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 1) % images.length);
  };

  // Function to move to the previous slide
  const prevSlide = () => {
    setCurrentIndex((prevIndex) => (prevIndex - 1 + images.length) % images.length);
  };

  // Toggle zoom state when image is clicked
  const toggleZoom = (e) => {
    e.stopPropagation(); // Prevent event from bubbling up
    setIsZoomed(!isZoomed);
    setIsPaused(true); // Pause autoplay when zoomed
  };

  // Close zoom view when clicking outside the image
  const closeZoom = () => {
    if (isZoomed) {
      setIsZoomed(false);
      setIsPaused(false); // Resume autoplay when zoom is closed
    }
  };

  // Handle escape key to close zoom view
  useEffect(() => {
    const handleEscKey = (event) => {
      if (event.key === 'Escape' && isZoomed) {
        setIsZoomed(false);
        setIsPaused(false);
      }
    };

    document.addEventListener('keydown', handleEscKey);
    return () => {
      document.removeEventListener('keydown', handleEscKey);
    };
  }, [isZoomed]);

  // Set up autoplay
  useEffect(() => {
    // Clear any existing timer
    if (timerRef.current) {
      clearInterval(timerRef.current);
    }
    
    // Only start the timer if not paused and not zoomed
    if (!isPaused && !isZoomed) {
      timerRef.current = setInterval(() => {
        nextSlide();
      }, autoplaySpeed);
    }
    
    // Clean up the timer when the component unmounts
    return () => {
      if (timerRef.current) {
        clearInterval(timerRef.current);
      }
    };
  }, [isPaused, isZoomed, autoplaySpeed, images.length]);

  // Pause autoplay when hovering
  const handleMouseEnter = () => setIsPaused(true);
  const handleMouseLeave = () => !isZoomed && setIsPaused(false);

  return (
    <>
      {isZoomed && (
        <div className="zoom-overlay" onClick={closeZoom}>
          <div className="zoom-container">
            <img 
              src={images[currentIndex]} 
              alt={`${alt} - Image ${currentIndex + 1}`} 
              className="zoom-image"
              onClick={(e) => e.stopPropagation()}
            />
            <button className="zoom-close-button" onClick={closeZoom}>
              ×
            </button>
          </div>
        </div>
      )}
      <div 
        className="slideshow-container"
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
      >
        <div className="corner-top-right"></div>
        <div className="corner-bottom-left"></div>
        <img 
          src={images[currentIndex]} 
          alt={`${alt} - Image ${currentIndex + 1}`} 
          className="slideshow-image"
          onClick={toggleZoom}
        />
        <div className="zoom-hint">
          <span>Click to zoom</span>
        </div>
        <div className="slideshow-controls">
          <button className="slideshow-button prev-button" onClick={prevSlide}>
            &#10094;
          </button>
          <div className="slideshow-indicators">
            {images.map((_, index) => (
              <span 
                key={index} 
                className={`slideshow-dot ${index === currentIndex ? 'active' : ''}`}
                onClick={() => setCurrentIndex(index)}
              ></span>
            ))}
          </div>
          <button className="slideshow-button next-button" onClick={nextSlide}>
            &#10095;
          </button>
        </div>
      </div>
    </>
  );
};

export default ImageSlideshow;
