import React, { useEffect, useRef } from "react";

// Simple procedural mist effect using canvas
function Mist() {
  const canvasRef = useRef(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    let animationId;
    const width = window.innerWidth;
    const height = window.innerHeight;
    canvas.width = width;
    canvas.height = height;

    // Mist cloud properties
    const clouds = Array.from({ length: 8 }).map(() => ({
      x: Math.random() * width,
      y: Math.random() * height * 0.4 + height * 0.05,
      r: 120 + Math.random() * 100,
      vx: 0.1 + Math.random() * 0.18,
      vy: (Math.random() - 0.5) * 0.08,
      alpha: 0.13 + Math.random() * 0.13,
    }));

    function draw() {
      ctx.clearRect(0, 0, width, height);
      clouds.forEach((c) => {
        ctx.save();
        ctx.globalAlpha = c.alpha;
        const grad = ctx.createRadialGradient(c.x, c.y, c.r * 0.6, c.x, c.y, c.r);
        grad.addColorStop(0, "#fff");
        grad.addColorStop(1, "#ede6f6");
        ctx.fillStyle = grad;
        ctx.beginPath();
        ctx.arc(c.x, c.y, c.r, 0, 2 * Math.PI);
        ctx.fill();
        ctx.restore();
        c.x += c.vx;
        c.y += c.vy;
        if (c.x - c.r > width) c.x = -c.r;
        if (c.y < 0 || c.y > height * 0.6) c.vy *= -1;
      });
      animationId = requestAnimationFrame(draw);
    }
    draw();
    return () => cancelAnimationFrame(animationId);
  }, []);

  return <canvas ref={canvasRef} className="mist-canvas" />;
}

export default Mist;
