import React, { useEffect, useState } from 'react';
import './ContactPage.css';
import underlineSvg from './assets/underline0.svg';

function ContactPage() {
  // Set SVG as CSS variable for the underline
  useEffect(() => {
    document.documentElement.style.setProperty('--underline-svg', `url(${underlineSvg})`);
  }, []);

  // Add scroll reveal effect
  useEffect(() => {
    const addScrollReveal = () => {
      const observer = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            entry.target.classList.add('revealed');
          }
        });
      }, { threshold: 0.1 });
      
      // Immediately reveal contact info items
      document.querySelectorAll('.cp-contact-info-item').forEach(el => {
        el.classList.add('revealed');
      });
      
      // Apply scroll reveal to other elements
      document.querySelectorAll('.cp-contact-title, .cp-contact-subtitle, .cp-contact-form-wrapper, .cp-contact-image-container').forEach(el => {
        el.classList.add('cp-reveal-element');
        observer.observe(el);
      });
    };
    
    addScrollReveal();
  }, []);

  const [formStatus, setFormStatus] = useState({
    submitted: false,
    error: false,
    message: ''
  });

  // Use environment variable for Basin endpoint with fallback
  const BASIN_ENDPOINT = process.env.REACT_APP_BASIN_ENDPOINT || 'https://usebasin.com/f/d27af3d6b144';
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    try {
      // Get form data
      const formData = new FormData(e.target);
      
      // Send form data to Basin endpoint
      const response = await fetch(BASIN_ENDPOINT, {
        method: 'POST',
        body: formData,
        headers: {
          'Accept': 'application/json'
        }
      });
      
      if (response.ok) {
        console.log('Form submitted successfully');
        setFormStatus({
          submitted: true,
          error: false,
          message: 'Thank you for your message! We will get back to you soon.'
        });
        
        // Reset form
        e.target.reset();
      } else {
        throw new Error('Form submission failed');
      }
    } catch (error) {
      console.error('Error submitting form:', error);
      setFormStatus({
        submitted: true,
        error: true,
        message: 'There was an error sending your message. Please try again later.'
      });
    }
  };

  useEffect(() => {
    const script = document.createElement('script');
    script.src = 'https://cdn.jsdelivr.net/npm/scrollreveal@4.0.9/dist/scrollreveal.min.js';
    script.async = true;
    document.body.appendChild(script);

    script.onload = () => {
      if (typeof ScrollReveal !== 'undefined') {
        const sr = ScrollReveal();
        sr.reveal('.cp-contact-title', { 
          duration: 1000, 
          origin: 'bottom',
          distance: '20px',
          delay: 200 
        });
        sr.reveal('.cp-contact-subtitle', { 
          duration: 1000, 
          origin: 'bottom',
          distance: '20px',
          delay: 400 
        });
        sr.reveal('.cp-contact-form-container', { 
          duration: 1000, 
          origin: 'bottom',
          distance: '30px',
          delay: 600 
        });
        sr.reveal('.cp-contact-info', { 
          duration: 1000, 
          origin: 'bottom',
          distance: '30px',
          delay: 800 
        });
      }
    };

    return () => {
      if (document.body.contains(script)) {
        document.body.removeChild(script);
      }
    };
  }, []);

  return (
    <div className="cp-contact-page">
      <div className="cp-contact-page-container">
        
        {/* Magical particles */}
        <div className="cp-magic-particles-container">
          {Array.from({ length: 20 }).map((_, index) => (
            <div 
              key={index}
              className={`cp-magic-particle cp-magic-particle-type-${index % 3}`}
              style={{
                left: `${Math.random() * 100}%`,
                top: `${Math.random() * 100}%`,
                width: `${Math.random() * 5 + 2}px`,
                height: `${Math.random() * 5 + 2}px`,
                backgroundColor: `hsl(${Math.random() * 60 + 260}, 100%, ${Math.random() * 30 + 60}%)`,
                animationDuration: `${Math.random() * 5 + 5}s`,
                animationDelay: `${Math.random() * 3}s`,
                opacity: 0.4 + (Math.random() * 0.4)
              }}
            />
          ))}
        </div>
        
        {/* Contact Section */}
        <section className="cp-contact-page-section">
          <div className="cp-contact-content">
            <h2 className="cp-contact-title">Send a Message</h2>
            <p className="cp-contact-subtitle" style={{ fontFamily: 'Poppins', fontWeight: 'bold', fontSize: '1.3rem' , marginBottom: '4rem'}}>Have questions or ideas? We'd love to hear from you!</p>
            
            <div className="cp-contact-form-container">
              <div className="cp-contact-form-wrapper">
                <form className="cp-contact-form" onSubmit={handleSubmit}>
                  <div className="cp-form-group">
                    <label htmlFor="name" className="cp-form-label">Name</label>
                    <input type="text" id="name" name="name" className="cp-form-input" placeholder="Your name" required />
                  </div>
                  <div className="cp-form-group">
                    <label htmlFor="email" className="cp-form-label">Email</label>
                    <input type="email" id="email" name="email" className="cp-form-input" placeholder="Your email" required />
                  </div>
                  <div className="cp-form-group">
                    <label htmlFor="subject" className="cp-form-label">Subject</label>
                    <input type="text" id="subject" name="subject" className="cp-form-input" placeholder="Subject" required />
                  </div>
                  <div className="cp-form-group">
                    <label htmlFor="message" className="cp-form-label">Message</label>
                    <textarea id="message" name="message" className="cp-form-textarea" placeholder="Your message" rows="5" required></textarea>
                  </div>
                  <div className="cp-button-container">
                    <div className="cp-to-label"><img src="/hero-page/logo-catsmith.svg" alt="Fablesmith Studio" className="cp-button-logo" /></div>
                    <button type="submit" className="cp-send-message-btn"><span>Send Message</span></button>
                  </div>
                  
                  {formStatus.submitted && (
                    <div className={`cp-form-message ${formStatus.error ? 'cp-error' : 'cp-success'}`}>
                      {formStatus.message}
                    </div>
                  )}
                </form>
              </div>
              <div className="cp-contact-image-container">
                <img src="/games-page/Urska1.png" alt="Urska Character" className="cp-contact-image" />
              </div>
            </div>
            
            <div className="cp-contact-info" style={{ display: 'flex', visibility: 'visible', opacity: 1 }}>
              <div className="cp-contact-info-item" style={{ display: 'block', visibility: 'visible', opacity: 1 }}>
                <h3>Email</h3>
                <p>nguye340@sheridancollege.ca</p>
              </div>
              <div className="cp-contact-info-item" style={{ display: 'block', visibility: 'visible', opacity: 1 }}>
                <h3>Find Our Games</h3>
                <div className="cp-game-links" style={{ display: 'flex', visibility: 'visible', opacity: 1 }}>
                  <a href="https://fablesmiths.itch.io/" target="_blank" rel="noopener noreferrer" className="cp-game-link" style={{ display: 'flex', visibility: 'visible', opacity: 1 }}>
                    <img src="/hero-page/logo-catsmith.svg" alt="Fablesmith Studio" className="cp-platform-icon" />
                    fablesmiths.itch.io
                  </a>
                </div>
              </div>
            </div>
          </div>
        </section>
        
        {/* Footer */}
        <footer className="cp-site-footer">
          <div className="cp-footer-content">
            <p className="cp-copyright">Copyright 2025 Fablesmiths. All rights reserved.</p>
          </div>
        </footer>
      </div>
    </div>
  );
}

export default ContactPage;
