// Based on original work by Radu-Daniel Vatavu and Jacob O. Wobbrock
// Reference: Vatavu, R.-D. (2017). Improving gesture recognition accuracy on touch screens for users with low vision.
// CHI '17 Proceedings. https://dl.acm.org/citation.cfm?id=3025941
