// This adaptation respects the original "New BSD License".

class GesturePoint {
    constructor(x, y, id, angle = 0.0) {
        this.x = x;
        this.y = y;
        this.id = id;
        this.angle = angle;
    }
}

class GestureTemplate {
    constructor(name, points) {
        this.name = name;
        this.points = points;
    }
}

class GestureResult {
    constructor(name, score, time) {
        this.name = name;
        this.score = score;
        this.time = time;
    }
}

class PPlusRecognizer {
  constructor() {
    this.templates = [];
    this.numPoints = 32;
  }
  
  addTemplate(name, points) {
    const processedPoints = this.preprocessPoints(points);
    const template = new GestureTemplate(name, processedPoints);
    this.templates.push(template);
    return this.templates.length;
  }
  
  recognize(points) {
    if (!points || points.length === 0) return null;
    
    const candidate = this.preprocessPoints(points);
    
    let minDistance = Infinity;
    let bestMatch = null;
    
    for (const template of this.templates) {
      const distance = this.cloudDistance(candidate, template.points);
      
      if (distance < minDistance) {
        minDistance = distance;
        bestMatch = template;
      }
    }
    
    if (!bestMatch) return null;
    
    // Convert distance to a score between 0 and 1
    // Improve recognition accuracy by using a more precise scoring formula
    // The divisor controls how strict the matching is (lower = stricter)
    const score = Math.max(0, 1 - minDistance / 150);
    
    return {
      name: bestMatch.name,
      score: score
    };
  }
  
  preprocessPoints(points) {
    // Resample to get a consistent number of points
    let newPoints = this.resample(points, this.numPoints);
    
    // Scale to a standard size
    newPoints = this.scale(newPoints);
    
    // Translate to origin
    newPoints = this.translateToOrigin(newPoints);
    
    return newPoints;
  }
  
  resample(points, n) {
    if (points.length === 0) return [];
    
    const newPoints = [{ ...points[0] }];
    const pathLength = this.pathLength(points);
    const interval = pathLength / (n - 1);
    let accumulatedDistance = 0;
    
    for (let i = 1; i < points.length; i++) {
      const distance = this.distance(points[i - 1], points[i]);
      
      if (accumulatedDistance + distance >= interval) {
        const qx = points[i - 1].x + ((interval - accumulatedDistance) / distance) * (points[i].x - points[i - 1].x);
        const qy = points[i - 1].y + ((interval - accumulatedDistance) / distance) * (points[i].y - points[i - 1].y);
        const newPoint = { x: qx, y: qy, id: points[i].id };
        
        newPoints.push(newPoint);
        points.splice(i, 0, newPoint);
        accumulatedDistance = 0;
      } else {
        accumulatedDistance += distance;
      }
    }
    
    // Sometimes we fall short of n points due to floating point errors
    while (newPoints.length < n) {
      newPoints.push({ ...points[points.length - 1] });
    }
    
    return newPoints;
  }
  
  scale(points) {
    if (points.length === 0) return [];
    
    // Find bounding box
    let minX = Infinity, maxX = -Infinity, minY = Infinity, maxY = -Infinity;
    
    for (const point of points) {
      minX = Math.min(minX, point.x);
      maxX = Math.max(maxX, point.x);
      minY = Math.min(minY, point.y);
      maxY = Math.max(maxY, point.y);
    }
    
    const size = Math.max(maxX - minX, maxY - minY);
    
    if (size === 0) return points; // Can't scale if all points are the same
    
    // Scale to 100x100
    const newPoints = [];
    for (const point of points) {
      newPoints.push({
        x: (point.x - minX) * 100 / size,
        y: (point.y - minY) * 100 / size,
        id: point.id
      });
    }
    
    return newPoints;
  }
  
  translateToOrigin(points) {
    if (points.length === 0) return [];
    
    // Find centroid
    let centroidX = 0, centroidY = 0;
    
    for (const point of points) {
      centroidX += point.x;
      centroidY += point.y;
    }
    
    centroidX /= points.length;
    centroidY /= points.length;
    
    // Translate to origin (0,0)
    const newPoints = [];
    for (const point of points) {
      newPoints.push({
        x: point.x - centroidX + 50, // Center at 50,50 (middle of 100x100 area)
        y: point.y - centroidY + 50,
        id: point.id
      });
    }
    
    return newPoints;
  }
  
  pathLength(points) {
    let length = 0;
    
    for (let i = 1; i < points.length; i++) {
      length += this.distance(points[i - 1], points[i]);
    }
    
    return length;
  }
  
  distance(p1, p2) {
    const dx = p2.x - p1.x;
    const dy = p2.y - p1.y;
    return Math.sqrt(dx * dx + dy * dy);
  }
  
  cloudDistance(points1, points2) {
    const n = points1.length;
    let totalDistance = 0;
    
    for (let i = 0; i < n; i++) {
      let minDistance = Infinity;
      
      for (let j = 0; j < n; j++) {
        const distance = this.distance(points1[i], points2[j]);
        minDistance = Math.min(minDistance, distance);
      }
      
      totalDistance += minDistance;
    }
    
    return totalDistance / n;
  }
}

const GestureUtils = {
    normalize(points) {
        let resampled = this.resample(points, 32);
        let scaled = this.scale(resampled);
        let translated = this.translateToOrigin(scaled);
        return this.computeAngles(translated);
    },

    resample(points, n) {
        const I = this.pathLength(points) / (n - 1);
        let D = 0.0;
        const newPoints = [points[0]];

        for (let i = 1; i < points.length; i++) {
            if (points[i].id === points[i - 1].id) {
                const d = this.distance(points[i - 1], points[i]);
                if ((D + d) >= I) {
                    const t = (I - D) / d;
                    const newX = points[i - 1].x + t * (points[i].x - points[i - 1].x);
                    const newY = points[i - 1].y + t * (points[i].y - points[i - 1].y);
                    const newPoint = new GesturePoint(newX, newY, points[i].id);
                    newPoints.push(newPoint);
                    points.splice(i, 0, newPoint);
                    D = 0.0;
                } else {
                    D += d;
                }
            }
        }

        if (newPoints.length === n - 1) {
            newPoints.push(new GesturePoint(
                points[points.length - 1].x,
                points[points.length - 1].y,
                points[points.length - 1].id
            ));
        }

        return newPoints;
    },

    scale(points) {
        let minX = Infinity, minY = Infinity, maxX = -Infinity, maxY = -Infinity;
        for (const p of points) {
            minX = Math.min(minX, p.x);
            minY = Math.min(minY, p.y);
            maxX = Math.max(maxX, p.x);
            maxY = Math.max(maxY, p.y);
        }
        const size = Math.max(maxX - minX, maxY - minY);
        return points.map(p => new GesturePoint((p.x - minX) / size, (p.y - minY) / size, p.id));
    },

    translateToOrigin(points) {
        const centroid = this.centroid(points);
        return points.map(p => new GesturePoint(p.x - centroid.x, p.y - centroid.y, p.id));
    },

    centroid(points) {
        const x = points.reduce((sum, p) => sum + p.x, 0) / points.length;
        const y = points.reduce((sum, p) => sum + p.y, 0) / points.length;
        return new GesturePoint(x, y, 0);
    },

    computeAngles(points) {
        const result = [points[0]];
        for (let i = 1; i < points.length - 1; i++) {
            const dx1 = points[i + 1].x - points[i].x;
            const dy1 = points[i + 1].y - points[i].y;
            const dx2 = points[i].x - points[i - 1].x;
            const dy2 = points[i].y - points[i - 1].y;
            const dot = dx1 * dx2 + dy1 * dy2;
            const norm = this.distance(points[i + 1], points[i]) * this.distance(points[i], points[i - 1]);
            const cosA = Math.max(-1, Math.min(1, dot / norm));
            const angle = Math.acos(cosA) / Math.PI;
            result.push(new GesturePoint(points[i].x, points[i].y, points[i].id, angle));
        }
        result.push(points[points.length - 1]);
        return result;
    },

    distance(p1, p2) {
        const dx = p2.x - p1.x;
        const dy = p2.y - p1.y;
        return Math.sqrt(dx * dx + dy * dy);
    },

    distanceWithAngle(p1, p2) {
        const weightAngle = 0.3;
        const dx = p2.x - p1.x;
        const dy = p2.y - p1.y;
        const dAngle = Math.abs(p2.angle - p1.angle);
        return Math.sqrt(dx * dx + dy * dy + weightAngle * dAngle * dAngle);
    },

    pathLength(points) {
        let length = 0;
        for (let i = 1; i < points.length; i++) {
            if (points[i].id === points[i - 1].id) {
                length += this.distance(points[i - 1], points[i]);
            }
        }
        return length;
    },

    cloudDistance(points1, points2) {
        const n = points1.length;
        const matched = new Array(n).fill(false);
        let sum = 0;
        
        for (let i = 0; i < n; i++) {
            let minDist = Infinity;
            let index = -1;
            
            for (let j = 0; j < n; j++) {
                if (!matched[j]) {
                    const dist = this.distanceWithAngle(points1[i], points2[j]);
                    if (dist < minDist) {
                        minDist = dist;
                        index = j;
                    }
                }
            }
            
            matched[index] = true;
            sum += minDist;
        }
        
        return sum / n;
    }
};

export { PPlusRecognizer, GesturePoint, GestureTemplate, GestureResult, GestureUtils };
