import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';

// Disable browser's automatic scroll restoration
if ('scrollRestoration' in window.history) {
  window.history.scrollRestoration = 'manual';
}

// Fix for sections disappearing when scrolling
window.addEventListener('DOMContentLoaded', () => {
  // Prevent any unwanted scroll behavior
  window.addEventListener('scroll', () => {
    // Ensure all sections remain visible
    document.querySelectorAll('.about-container, .game-teaser, .hon-game-section, .team-section, .contact-section').forEach(section => {
      section.style.visibility = 'visible';
      section.style.display = 'flex';
    });
  });
});

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);
